
import HeaderBar from '../../components/header.vue'
import FooterBar from '../../components/footer.vue'
import Income from '../../pages/report/income.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

const list = () => ['', '', '']

export default {
  components: {
    Tab,
    TabItem,
    Swiper,
    SwiperItem,
    HeaderBar,
    FooterBar,
    Flexbox,
    FlexboxItem,
    XButton,
    Datetime,
    Group,
    Income
  },
  methods: {
    onItemClick(index) {
      if (index === '') {
        this.$data.showContent001 = true
      } else {
        this.$data.showContent001 = false
      }
      console.log('on item click:', index)
    },
    change1(value) {
      console.log('change', value)
    },
    change2(value) {
      console.log('change', value)
    },
    setToday(value) {
      let now = new Date()
      let cmonth = now.getMonth() + 1
      let day = now.getDate()
      if (cmonth < 10) cmonth = '0' + cmonth
      if (day < 10) day = '0' + day
      this.$data.startDate = now.getFullYear() + '-' + cmonth + '-' + day
      console.log('set today ok')
    },
    load(uuid) {
      setTimeout(() => {
        this.$refs.scroller.donePulldown()
      }, 2000)
    }
  },
  data() {
    return {
      startDate: '2016-11-11',
      endDate: '2017-10-11',
      list2: list(),
      demo2: '',
      index: 0,
      showContent001: true,
      showContent002: false,
      list: [{
        label: '',
        value: ''
      }, {
        label: '',
        value: '101 '
      }, {
        label: '',
        value: '7500'
      }],
      buttons1: [{
        style: 'default',
        text: '',
        onButtonClick: (name) => {
          alert(`clicking ${name}`)
        }
      }, {
        style: 'primary',
        text: '',
        onButtonClick: (name) => {
          alert(`clicking ${name}`)
        }
      }]
    }
  }
}



